import React, { useState, useLayoutEffect } from 'react';

const Desc = 'Generating Global Environmental Benefits.';

const Size = Desc.length;

const Tick = 100; // in miliseconds

export default () => {

    const [idx, setIdx] = useState(0);

    useLayoutEffect(() => {
        if (idx < Size) {
            window.setTimeout(
                () => setIdx(idx + 1),
                Math.floor(Math.random() * Tick, 10) + 50,
            )
        }
    });

    return (
        <h2 className="logo-description">
            {Desc.slice(0, idx)
                .split('')
                .map((ltr, key) => <span key={key}>{ltr}</span>)
            }
            <i className="fa fa-i-cursor" style={{
                fontWeight: "normal",
                color: "rgba(0, 0, 0, 0.3)"
            }} />
        </h2>
    );
}